// background: svg-load('../blocks/phones-modal/bg-img/phone.svg', fill=$main-color, stroke=rgba(255, 255, 255, 0));

.prod-code {
  display: block;
  //vertical-align: baseline;
  margin: 10px 0;
  font-size: 14px;
  font-weight: 400;
  color: $text-muted;
}

.label-top-left {
  position: absolute;
  left: 0;
  top: 20px;
  display: block;
  padding: 5px 15px 6px;
  font-size: 18px;
  font-weight: 700;
  color: $white;
  background-color: $main-color;
  border-top-right-radius: $border-radius;
  border-bottom-right-radius: $border-radius;
  pointer-events: none;
}

.publishedon {
  font-size: 1em;
  color: $text-muted;
}

.embed-responsive {

  img {
    object-fit: contain;
    object-position: center;
  }
}

.b-lazy {
  transition: opacity 500ms ease-in-out;
  max-width: 100%;
  opacity: 0;
}

.b-lazy.b-loaded {
  opacity: 1;
}

.main-content {
  position: relative;
  padding-bottom: 20px;

  @media (min-width: $screen-md) {
    padding-bottom: 40px;
  }

  &--overlayed {

    &::before {
      display: block;
      content: "";
      position: absolute;
      z-index: 2;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background-color: $black;
      opacity: 0.5;
    }
  }
}

// Swiper

.swiper-button-prev,
.swiper-button-next {
  display: block;
  content: "";
  position: absolute;
  z-index: 5;
  top: 50%;
  transform: translateY(calc(-50% - 10px));
  width: 19px;
  height: 30px;
  padding: 5px;
  background-repeat: no-repeat !important;
  background-size: 9px 20px;
  background-position: center !important;
  cursor: pointer;

  // &[aria-disabled=true] {
  //   opacity: 0.5;
  //   pointer-events: none;
  // }
}

.swiper-button-prev {
  left: 0;
  background: svg-load('../blocks/prod-slider/bg-img/left-arrow-icon.svg', fill=$text-muted);

  &:hover,
  &:focus {
    background: svg-load('../blocks/prod-slider/bg-img/left-arrow-icon.svg', fill=$main-color);
  }
}

.swiper-button-next {
  right: 0;
  background: svg-load('../blocks/prod-slider/bg-img/right-arrow-icon.svg', fill=$text-muted);

  &:hover,
  &:focus {
    background: svg-load('../blocks/prod-slider/bg-img/right-arrow-icon.svg', fill=$main-color);
  }
}
